import React, {useState, useEffect} from 'react'

const Scroll = () => {

    const [ScrollY, setScrollY] = useState(0); //  
    const [BtnStatus, setBtnStatus] = useState(false); //  
  
    const handleScroll = () => {
      setScrollY(window.pageYOffset); // window   ScrollY 
      if (ScrollY > 100) {
        setBtnStatus(true);
      } else {
        setBtnStatus(false);
      }
    };
  
    const handleTop = () => {
      //     
      window.scrollTo({
        top: 0,
        behavior: "smooth",
      });
      setScrollY(0); // ScrollY   
      setBtnStatus(false); //  
    };
  
    useEffect(() => {
      console.log("ScrollY is ", ScrollY); // ScrollY    
    }, [ScrollY]);
  
    useEffect(() => {
      const watch = () => {
        window.addEventListener("scroll", handleScroll);
      };
      watch(); // addEventListener  
      return () => {
        window.removeEventListener("scroll", handleScroll); // addEventListener  
      };
    });

  return (
    <div>
      <button
        className={BtnStatus ? "topBtn active" : "topBtn"} //   
        onClick={handleTop} //    
      >
      TOP
      </button>
    </div>
  )
}

export default Scroll